'use client';

import Link from 'next/link';
import Image from 'next/image';
import { useState, useEffect } from 'react';

export default function Navigation() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [metalsOpen, setMetalsOpen] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 20);
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Body scroll lock when mobile menu is open
  useEffect(() => {
    if (mobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => { document.body.style.overflow = 'unset'; };
  }, [mobileMenuOpen]);

  const navLinks = [
    { href: '/metals', label: 'Metals', hasDropdown: true },
    { href: '/buy-sell', label: 'Buy & Sell' },
    { href: '/staking', label: 'Staking' },
    { href: '/trust-center', label: 'Trust Center' },
    { href: '/how-it-works', label: 'How It Works' },
  ];

  const metalLinks = [
    { href: '/metals/auxg', symbol: 'AUXG', name: 'Gold', icon: '/metals/gold.png' },
    { href: '/metals/auxs', symbol: 'AUXS', name: 'Silver', icon: '/metals/silver.png' },
    { href: '/metals/auxpt', symbol: 'AUXPT', name: 'Platinum', icon: '/metals/platinum.png' },
    { href: '/metals/auxpd', symbol: 'AUXPD', name: 'Palladium', icon: '/metals/palladium.png' },
  ];

  return (
    <>
      {/* Soft Launch Banner */}
      <div style={{
        background: 'linear-gradient(to right, rgba(120,53,15,0.9), rgba(146,64,14,0.9), rgba(120,53,15,0.9))',
        color: '#FDE68A',
        padding: '10px 16px',
        textAlign: 'center',
        fontSize: '13px',
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 60,
        letterSpacing: '0.02em'
      }}>
        <span style={{ display: 'inline-flex', alignItems: 'center', gap: '10px', flexWrap: 'wrap', justifyContent: 'center' }}>
          <span style={{ width: '6px', height: '6px', background: '#FBBF24', borderRadius: '50%', boxShadow: '0 0 8px #FBBF24' }}></span>
          <span style={{ fontWeight: 500 }}>Soft Launch Active</span>
          <span className="hide-mobile" style={{ color: '#FCD34D', opacity: 0.7 }}>â€¢</span>
          <span className="hide-mobile" style={{ opacity: 0.9 }}>Real transactions with temporary limits</span>
        </span>
      </div>

      {/* Main Navigation */}
      <nav style={{
        position: 'fixed',
        top: '38px',
        left: 0,
        right: 0,
        zIndex: 50,
        transition: 'all 0.3s ease',
        background: isScrolled ? 'rgba(10,15,28,0.98)' : 'transparent',
        backdropFilter: isScrolled ? 'blur(20px)' : 'none',
        borderBottom: isScrolled ? '1px solid rgba(255,255,255,0.05)' : 'none'
      }}>
        <div style={{ maxWidth: '1280px', margin: '0 auto', padding: '0 24px' }}>
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', height: '72px' }}>
            {/* Logo */}
            <Link href="/" style={{ display: 'flex', alignItems: 'center', gap: '12px', textDecoration: 'none' }}>
              <Image 
                src="/auxite-wallet-logo.png" 
                alt="Auxite" 
                width={140} 
                height={40} 
                style={{ objectFit: 'contain' }}
              />
            </Link>

            {/* Desktop Navigation */}
            <div className="desktop-nav" style={{ display: 'flex', alignItems: 'center', gap: '36px' }}>
              {navLinks.map((link) => (
                <div 
                  key={link.href} 
                  style={{ position: 'relative' }}
                  onMouseEnter={() => link.hasDropdown && setMetalsOpen(true)}
                  onMouseLeave={() => link.hasDropdown && setMetalsOpen(false)}
                >
                  <Link 
                    href={link.href} 
                    style={{ 
                      color: '#A1A1AA', 
                      textDecoration: 'none', 
                      fontWeight: 500, 
                      fontSize: '14px',
                      transition: 'color 0.2s',
                      display: 'flex',
                      alignItems: 'center',
                      gap: '4px'
                    }}
                  >
                    {link.label}
                    {link.hasDropdown && (
                      <svg style={{ width: '12px', height: '12px', transition: 'transform 0.2s', transform: metalsOpen ? 'rotate(180deg)' : 'rotate(0)' }} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                      </svg>
                    )}
                  </Link>
                  
                  {/* Dropdown */}
                  {link.hasDropdown && metalsOpen && (
                    <div style={{
                      position: 'absolute',
                      top: '100%',
                      left: '-20px',
                      paddingTop: '12px',
                    }}>
                      <div style={{
                        background: 'rgba(20,27,45,0.98)',
                        backdropFilter: 'blur(20px)',
                        border: '1px solid rgba(255,255,255,0.08)',
                        borderRadius: '16px',
                        padding: '8px',
                        minWidth: '220px',
                        boxShadow: '0 20px 40px rgba(0,0,0,0.4)'
                      }}>
                        {metalLinks.map((metal) => (
                          <Link 
                            key={metal.href} 
                            href={metal.href}
                            style={{
                              display: 'flex',
                              alignItems: 'center',
                              gap: '12px',
                              padding: '12px 16px',
                              borderRadius: '10px',
                              textDecoration: 'none',
                              transition: 'background 0.2s'
                            }}
                          >
                            <Image src={metal.icon} alt={metal.name} width={32} height={32} />
                            <div>
                              <div style={{ color: 'white', fontWeight: 600, fontSize: '14px' }}>{metal.symbol}</div>
                              <div style={{ color: '#71717A', fontSize: '12px' }}>{metal.name}</div>
                            </div>
                          </Link>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>

            {/* Desktop Sign In Button */}
            <Link href="https://wallet.auxite.io" className="desktop-nav" style={{
              background: 'linear-gradient(135deg, #D4AF37 0%, #F4E4BC 50%, #D4AF37 100%)',
              color: '#0A0F1C',
              padding: '10px 24px',
              borderRadius: '10px',
              fontWeight: 600,
              fontSize: '14px',
              textDecoration: 'none',
              boxShadow: '0 4px 20px rgba(212,175,55,0.3)',
              transition: 'all 0.2s'
            }}>
              Sign In
            </Link>

            {/* Hamburger Button */}
            <div 
              className={`hamburger ${mobileMenuOpen ? 'open' : ''}`}
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            >
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        </div>
      </nav>

      {/* Mobile Menu */}
      <div className={`mobile-menu ${mobileMenuOpen ? 'open' : ''}`}>
        {navLinks.map((link) => (
          <Link 
            key={link.href} 
            href={link.href}
            onClick={() => setMobileMenuOpen(false)}
          >
            {link.label}
          </Link>
        ))}
        
        {/* Mobile Metal Links */}
        <div style={{ marginTop: '16px', paddingTop: '16px', borderTop: '1px solid rgba(255,255,255,0.1)' }}>
          <div style={{ color: '#71717A', fontSize: '12px', marginBottom: '12px', textTransform: 'uppercase', letterSpacing: '0.1em' }}>Metals</div>
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '12px' }}>
            {metalLinks.map((metal) => (
              <Link 
                key={metal.href} 
                href={metal.href}
                onClick={() => setMobileMenuOpen(false)}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: '10px',
                  padding: '12px',
                  background: 'rgba(255,255,255,0.03)',
                  borderRadius: '12px',
                  textDecoration: 'none',
                  border: '1px solid rgba(255,255,255,0.05)'
                }}
              >
                <Image src={metal.icon} alt={metal.name} width={28} height={28} />
                <div>
                  <div style={{ color: 'white', fontWeight: 600, fontSize: '14px' }}>{metal.symbol}</div>
                  <div style={{ color: '#71717A', fontSize: '11px' }}>{metal.name}</div>
                </div>
              </Link>
            ))}
          </div>
        </div>

        {/* Mobile Sign In */}
        <Link 
          href="https://wallet.auxite.io" 
          onClick={() => setMobileMenuOpen(false)}
          style={{
            marginTop: 'auto',
            background: 'linear-gradient(135deg, #D4AF37 0%, #B8960C 100%)',
            color: '#0A0F1C',
            padding: '16px 24px',
            borderRadius: '12px',
            fontWeight: 600,
            fontSize: '16px',
            textDecoration: 'none',
            textAlign: 'center',
            boxShadow: '0 8px 32px rgba(212,175,55,0.3)'
          }}
        >
          Sign In to Wallet
        </Link>
      </div>
    </>
  );
}
